/* Colors */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Landing Page */
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  font-family: "DM Sans", Arial, Helvetica, sans-serif;
  font-size: 18px;
  max-width: 1300px;
  margin: 0 auto;
}

p {
  margin-bottom: 0;
  padding: 0.5vw;
}

img {
  padding-right: 1rem;
}